.flip-card {
    background-color: transparent;
    width: 300px;
    height: 300px;
    perspective: 1000px;
  }
  
  .flip-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  }
  
  .flip-card:hover  .flip-card-inner {
    transform: rotateY(180deg);
  }
  
  .flip-card-front, .flip-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  
  .flip-card-front {
    background-color: #bbb;
    color: black;
  }
  
  .flip-card-back {
    border-radius: 15px;
    background-color:rgb(24, 49, 83);
    color: white;
    transform: rotateY(180deg);
  }

  .flip-card-back h1 {
    margin: auto;
    display: inline-block;
    text-align: center;
    margin-top: 7px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: aliceblue;
    font-size: 20px;
  }

  .flip-card-back p {
    justify-content: left;
    text-align: left;
    font-weight: 600;
    margin-left: 12px;
    margin-right: 12px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: rgb(255, 255, 255);
    font-size: 14px;
  }
  .flip-card-back a {
    justify-content: left;
    text-align: left;
   
    
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    color: rgb(255, 255, 255);
    font-size: 14px;
  }

  .flex-container {
    
    display: flex;
    padding-left: 6%;
    flex-wrap: wrap;
    height: auto;
    margin: auto;
    align-content: space-between;
    flex-direction: row;
    background-color: DodgerBlue;
  }
  
  .flex-div {
    background-color: #000000;
    width: fit-content;
    margin-left: 10%;
    
    margin-top: 15px;
    margin-bottom: 15px;
    text-align: center;
    font-size: 30px;
  }

  @media only screen and (max-width: 800px) {
 

      .flex-container {
        padding-left: 0%;
        display: flex;
        flex-wrap: wrap;
        align-content: space-between;
        flex-direction: column;
        background-color: DodgerBlue;
      }
  }
